import React, { useEffect, useState } from "react";
import Axios from "axios";
import { Row, Col, Card, Button, Divider } from "antd";
import ImageSlider from "../../utils/ImageSlider";
import CheckBox from "./Sections/CheckBox";
import RadioBox from "./Sections/RadioBox";
import SearchFeature from "./Sections/SearchFeature";
import { regions, price } from "./Sections/Datas";
import "./Sections/Landing.css";
import { Carousel } from "antd";
import { DownOutlined, CompassOutlined } from "@ant-design/icons";
// import LoadMore from "../../utils/LoadMore";
import { Link } from "react-router-dom";

const { Meta } = Card;

function LandingPage() {
  const [Products, setProducts] = useState([]);
  const [Skip, setSkip] = useState(0);
  const [Limit, setLimit] = useState(6);
  const [PostSize, setPostSize] = useState();
  const [SearchTerms, setSearchTerms] = useState("");

  const [Filters, setFilters] = useState({
    regions: [],
    price: [],
  });
  const [showLoadMore, handleShowLoadMore] = useState(false);

  useEffect(() => {
    window.addEventListener("scroll", () => {
      if (window.innerHeight + window.scrollY >= document.body.scrollHeight) {
        handleShowLoadMore(true);
      } else handleShowLoadMore(false);
    });
    return () => {
      window.removeEventListener("scroll", LandingPage, true);
    };
  }, []);

  useEffect(() => {
    const variables = {
      skip: Skip,
      limit: Limit,
    };

    getProducts(variables);
  }, []);

  function formatNumber(num) {
    return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
  }

  const getProducts = (variables) => {
    Axios.post("/api/product/getProducts", variables).then((response) => {
      if (response.data.success) {
        if (variables.loadMore) {
          setProducts([...Products, ...response.data.products]);
        } else {
          setProducts(response.data.products);
        }
        setPostSize(response.data.postSize);
      } else {
        alert("Failed to fectch product datas");
      }
    });
    console.log(Products);
  };

  const onLoadMore = () => {
    let skip = Skip + Limit;

    const variables = {
      skip: skip,
      limit: Limit,
      loadMore: true,
      filters: Filters,
      searchTerm: SearchTerms,
    };
    getProducts(variables);
    setSkip(skip);
  };

  const renderCards = Products.map((product, index) => {
    return (
      <Col lg={8} md={12} xs={24} key={index}>
        <Card
          style={{ border: "1px solid #ddd" }}
          className="landingCard"
          hoverable={true}
          cover={
            <div className="cardCover">
              <ImageSlider dots={false} images={product.images} />
              <div className="cardDescription">
                <h2>{product.location}</h2>
                <br />
                <h4>{product.description}</h4>
              </div>
            </div>
          }
        >
          <h3 className="price">
            From ₱{formatNumber(`${product.price}`)} p.p
          </h3>
          <Meta
            title={product.title}
            description={`Difficulty: ${product.difficulty}/10`}
          />
          <Button
            style={{
              position: "absolute",
              right: "10px",
              bottom: "15px",
            }}
          >
            {" "}
            <Link to={`/product/${product._id}`}>Learn More</Link>
          </Button>
        </Card>
      </Col>
    );
  });

  const showFilteredResults = (filters) => {
    const variables = {
      skip: 0,
      limit: Limit,
      filters: filters,
    };
    getProducts(variables);
    setSkip(0);
  };

  const handlePrice = (value) => {
    const data = price;
    let array = [];

    for (let key in data) {
      if (data[key]._id === parseInt(value, 10)) {
        array = data[key].array;
      }
    }
    console.log("array", array);
    return array;
  };

  const handleFilters = (filters, category) => {
    const newFilters = { ...Filters };

    newFilters[category] = filters;

    if (category === "price") {
      let priceValues = handlePrice(filters);
      newFilters[category] = priceValues;
    }

    console.log(newFilters);

    showFilteredResults(newFilters);
    setFilters(newFilters);
  };

  const updateSearchTerms = (newSearchTerm) => {
    const variables = {
      skip: 0,
      limit: Limit,
      filters: Filters,
      searchTerm: newSearchTerm,
    };

    setSkip(0);
    setSearchTerms(newSearchTerm);

    getProducts(variables);
  };

  return (
    <div>
      <div className="showcase" style={{ textAlign: "center" }}>
        {" "}
        <h2>It's time for new</h2>
        <Carousel
          pauseOnHover={false}
          speed={1000}
          autoplaySpeed={3000}
          autoplay
          draggable={false}
          swipe={false}
          dotPosition="left"
        >
          <h1>ADVENTURES</h1>
          <h1>THRILLS</h1>
          <h1>EXPERIENCES</h1>
          <h1>ESCAPES</h1>
        </Carousel>
        <Link to="/search">
          <button>Book Tours</button>
        </Link>
        <a className="next" href="#cardcontent">
          <span>
            <DownOutlined />
          </span>
          <span>scroll&nbsp;down</span>
        </a>
      </div>
      <div
        id="cardcontent"
        style={{ maxWidth: "1500px", margin: "auto", padding: "0.5rem" }}
      >
        <div
          style={{
            display: "flex",
            alignItems: "center",
            flexDirection: "column",
            margin: "1rem auto",
          }}
        >
          <Divider>
            {" "}
            <h1 className="book">
              <CompassOutlined />
              &nbsp;BOOK TOURS
            </h1>
          </Divider>
          <br />
          <Card
            className="searchCard"
            hoverable
            style={{
              width: "100%",
              padding: "0.5rem",
              border: "1px solid #ddd",
            }}
          >
            <Row justify="space-between" align="middle" gutter={16}>
              <Col xs={24} sm={12} lg={16}>
                <h1 className="choose">
                  Choose your adventure from the
                  <br /> widest range of tours in the Philippines
                </h1>
              </Col>
              <Col xs={24} sm={12} lg={8}>
                {" "}
                <SearchFeature refreshFunction={updateSearchTerms} />
              </Col>
            </Row>
          </Card>
        </div>

        {Products.length === 0 ? (
          <div
            style={{
              display: "flex",
              height: "300px",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <h2>No post yet...</h2>
          </div>
        ) : (
          <div>
            <Row gutter={[16, 16]}>{renderCards}</Row>
          </div>
        )}
        <br />
        <br />

        {PostSize >= Limit && (
          <div
            style={{
              display: "flex",
              justifyContent: "center",
            }}
          >
            <Button
              className={`${showLoadMore && "add"}`}
              type="primary"
              onClick={onLoadMore}
              style={{
                position: "fixed",
                bottom: "300px",
                visibility: "hidden",
                transition: "0.3s ease-out",
                transform: "translateY(20px)",
                opacity: "0",
              }}
            >
              Load More
            </Button>
          </div>
        )}
      </div>
    </div>
  );
}

export default LandingPage;
